.highlight {
    display: block;
    position: absolute;
    height: 25px;
    width: 25px;
    left: 10px;
    top: 10px;
    margin-left: -10px;
    margin-top: -10px;
    border-radius: 50%;
    opacity: 0.5;
    pointer-events: none;
    background: radial-gradient(
            rgb(231, 201, 174),
            rgba(123, 123, 123, 0)
    );
}

.bgc a {
    padding: 15px 25px;
    display: block;
    margin-top: -14px;
    margin-bottom: -14px;
    cursor: default;
}

.bgc:hover a {
    background-color: #453e34;
}

.isActive {
    background-color: #342f28;
    margin-top: -14px;
    margin-bottom: -15px;
}
