.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 95vh;
    padding-top: 10vmax;
    padding-left: 5vmax;
    padding-right: 5vmax;
    color: #cadbf5;
    border-radius: 10px;
}

.wrapper:hover {
    background: #26436d;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Varela Round', sans-serif;

}

.info {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 50px;
}

.info h2 {
    font-size: 2vmax;
    margin-top: 0;
    margin-bottom: 0;
    padding-right: 30px;
    text-align: center;
}

.wins_losses {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-family: 'Varela Round', sans-serif;
    font-size: 1.2vmax;
    text-align: center;
    line-height: 1;
}

.wins,
.losses {
    display: flex;
    align-items: center;
}

.wins .number,
.losses .number {
    font-size: 3vmax;
    margin-right: 5px;
}
.wins .text,
.losses .text {
    margin-left: 5px;
    margin-right: 5px;
    text-decoration: underline;
}

.wins {
    margin-right: 30px;
}

.wins .number {
    color: #5dc674;
}

.losses .number {
    color: #da516f;
}

.choices {
    display: grid;
    grid-template-columns: 3fr 1fr 3fr;
    text-align: center;
    grid-gap: 20px;
    font-family: 'Varela Round', sans-serif;
    font-size: 1.4vmax;
    color: #5f99f0;
}

.choices > div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.vs {
    width: 100px;
    font-size: 100px;
    opacity: 0.25;
    transform: rotate(-0.1turn);
    pointer-events: none;
}

.choices button {
    cursor: pointer;
    font-size: 50px;
    margin-bottom: 20px;
    padding: 20px;
    background: #fff;
    outline: none;
    border: none;
    border-radius: 50%;
    height: 100px;
    width: 100px;
    transition: 0.1s ease all;
}

.choices button:hover {
    transform: scale(1.07);
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.choices .rock {
    background: #f7941a;
}

.choices .rock path {
    fill: #815116;
}

.choices .paper {
    background: #8dc351;
}

.choices .paper path {
    fill: #3f5e1b;
}

.choices .scissors {
    background: #7c79ea;
}

.choices .scissors path {
    fill: #2f2c8f;
}

.choices .computer_choice {
    background: #333;
    color: #bbb;
}

.choices .computer_choice path {
    fill: #bbb;
}

.game_state {
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    line-height: 1.3;
    font-family: 'Varela Round', sans-serif;
}

.game_state.win > div {
    background: rgb(141, 195, 81);
    color: rgb(226, 243, 206);
}

.game_state.win svg path {
    fill: rgb(226, 243, 206);
}

.game_state.draw > div {
    background: rgb(0, 0, 0);
    color: rgb(88, 88, 88);
}

.game_state.draw svg path {
    fill: rgb(88, 88, 88);
}

.game_state.lose > div {
    background: rgb(218, 81, 111);
    color: rgb(240, 169, 184);
}

.game_state.lose svg path {
    fill: rgb(240, 169, 184);
}

.game_state > div {
    width: 60%;
    text-align: center;
    padding: 0 40px;
    border-radius: 10px;
    z-index: 1;
    color: #fff;
    font-size: 60px;
}

.game_state_content {
    display: flex;
    justify-content: center;
}

.game_state_content p {
    margin: 40px 15px 30px 0;
    display: flex;
    align-items: center;
}

.game_state_content svg {
    width: 100px;
}

.game_state button {
    margin-bottom: -5%;
    width: 100%;
    display: block;
    border: none;
    border-radius: 10px;
    padding: 25px;
    font-size: 30px;
    cursor: pointer;
    flex-shrink: 1;
    background: #fa65ff;
    color: #fdedfd;
}
